.contacts {

    @media (min-width: $desktop) {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 11px;
        padding-bottom: 10px;
        width: 100%;
    }

    &__wrapper {
        
        @media (min-width: $desktop) {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 1280px;
            margin: 0 auto;
        }
    }

    &__inner {
        display: none;
        color: #fff;

        @media (min-width: $desktop) {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 1010px;
        }
    }

    &__phones-wrapper {

        @media (min-width: $desktop) {
            display: flex;
            justify-content: space-between;
            width: 636px;
        }
    }

    &__title {
        
        @media (min-width: $desktop) {
            
            font-size: 14px;
            
            line-height: 17px;
            letter-spacing: 0.11em;
        }
    }

    &__phones-block {
        @media (min-width: $desktop) {
            font-family: 'Carter One', cursive;
            font-size: 16px;
            font-weight: 400;
            line-height: 25px;
            letter-spacing: 0.11em;
        }
    }

    &__phone {
        position: relative;

        @media (min-width: $desktop) {
            display: inline;
            margin-right: 38px;
            color: #fff;
        }

        &::after {
            position: absolute;
            content: '';
            height: 25px;
            width: 1px;
            background-color: #BEBEBE;
            right: -19px;
        }

        &:last-child {

            @media (min-width: $desktop) {
            
                margin-right: 0;
            }

            &::after {
                display: none;
            }
        }
    }

    &__watsapp-block {
        color: #fff;
    }

    &__watsapp {
        font-size: 16px;
        line-height: 20px;
        letter-spacing: 0.11em;
        margin-right: 18px;
    }

    &__watsapp-time {
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.11em;
    }

    &__callback-inner,
    &__callback {
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;
        text-transform: uppercase;
        color: #fff;

        padding-top: 12px;
        padding-bottom: 12px;
        padding-left: 28px;
        padding-right: 26px;

        background-color: #E61A23;
        border: 1px solid #FEFEFE;
        border-radius: 5px;

        &:hover,
        &:focus {
            opacity: 0.8;
        }

        &:active {
            opacity: 0.6;
        }
    }
}