.answers {

    padding-left: 15px;
    padding-right: 15px;
    
    @media (min-width: $tablet) {
        padding-left: 30px;
        padding-right: 30px;
    }
    

    @media (min-width: $desktop) {
        width: 1280px;
        margin: 0 auto;
    }

    &__item {
        margin-bottom: 14px;
        
        @media (min-width: $tablet) {
            margin-bottom: 20px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__title-wrapper {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        

        
    }


    &__content {
        display: none;

        @media (min-width: $tablet) {
        }

        
    }
    

    &__btn {
        font-family: 'Yanone Kaffeesatz', sans-serif;
        display: inline-block;
        position: relative;
        font-size: 25px;
        font-weight: 400;
        line-height: 105%;
        width: 100%;
        padding-bottom: 10px;
        
        cursor: pointer;

        &:after {
            position: absolute;
            content: '';
            width: 100%;
            height: 1px;
            background-color: #E61A23;
            bottom: 0;
            right: 0;
        }

        @media (min-width: $tablet) {
            font-size: 30px;
        }

        @media (min-width: $desktop) {
        }

        
    }

    
    &__text {

        padding-top: 10px;
    }


    &__icon {
        position: absolute;
        stroke: #332B2B;
        right: 0;
        top: 50%;
        margin-top: -8.5px;
        display: none;
        transition: 0.3s;

        &:hover, 
        &:focus {
            opacity: 0.6;
            transition: 0.3s;
        }

        @media (min-width: $tablet) {
            display: block;
        }

    }
    
}


.answers__switch:checked ~ .answers__content {
    display: block;  
    
}


.answers__switch:checked ~ .answers__btn .answers__icon {
    transform: rotate(-180deg);
}


