.program {


    @media (min-width: $desktop) {
        width: 1280px;
        margin: 0 auto;
    }

    &__wrapper{ 
        

        @media (min-width: $desktop) {
            display: flex;
            justify-content: space-between;
        }
    }

    &__title-wrapper {
        color: #fff;
        background-color: #E61A23;
        padding-left: 15px;
        padding-right: 15px;
        padding-top: 30px;
        padding-bottom: 30px;
        margin-bottom: 20px;
        position: relative;

        @media (min-width: $tablet) {
            padding-left: 30px;
            padding-right: 30px;
            padding-bottom: 40px;
        }

        @media (min-width: $desktop) {
            width: 408px;
            background-color: transparent;
            margin: 0;padding-left: 0;
            

            &::before {
                position: absolute;
                content: '';
                height: 100%;
                width: 50vw;
                z-index: -1;
                top: 0;
                right: 0;
                background-color: #E61A23;
            }
        }

        
    }

    &__title {
        margin-bottom: 20px;
        color: #fff;

        @media (min-width: $tablet) {
        }

        @media (min-width: $desktop) {
            margin-bottom: 15px;
        }
    }

    &__steps-wrapper {
        margin-left: 15px;
        margin-right: 15px;

        @media (min-width: $desktop) {
            margin: 0;
            width: 842px;
        }
    }

    &__step-wrapper {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }

        @media (min-width: $tablet) {
            margin-bottom: 40px;
        }

        @media (min-width: $desktop) {
            margin-bottom: 20px;
        }
    }

    &__step-title {
        font-family: 'Yanone Kaffeesatz', sans-serif;
        font-size: 25px;
        font-weight: 400;
        line-height: 105%;
        margin-bottom: 15px;
        color: #332B2B;

        @media (min-width: $tablet) {
            margin-bottom: 20px;
        }

        @media (min-width: $desktop) {
            margin-bottom: 10px;
        }
    }

    
}