.articles {


    @media (min-width: $tablet) {
        padding-left: 30px;
        padding-right: 30px;
    }

    @media (min-width: $desktop) {
        width: 1280px;
        padding: 0;
        margin: 0 auto;
    }

    &__title {

        margin-left: 15px;
        margin-right: 15px;

        @media (min-width: $tablet) {
            margin-left: 30px;
            margin-right: 30px;
        }

        @media (min-width: $desktop) {
            margin-left: 0;
            margin-right: 0;
        }
    }

    &__list {
        display: grid;
        row-gap: 30px;

        @media (min-width: $desktop) {
            
            grid-template-columns: 1fr 1fr;
            column-gap: 30px;
            row-gap: 30px;
        }
    }

    &__item {
        margin-bottom: 20px;
        background-color: #fff;
        box-shadow: 0px 8px 25px rgba(220, 220, 220, 0.25);

        @media (min-width: $tablet) {
        }

        @media (min-width: $desktop) {
            margin-bottom: 0;
        }
    }
}



.article__text--inner {
    grid-row-start: 2;
    grid-column-end: 5;

    &:last-of-type {
        background-color: yellowgreen;
    }
}

