.advantages {

    font-family: 'Yanone Kaffeesatz', sans-serif;
    width: 290px;
    
    padding-top: 30px;
    padding-left: 28px;
    padding-right: 28px;
    margin: 0 auto;

    background-color: #fff;
    box-shadow: 0px 0px 20px rgba(189, 185, 185, 0.25);
    border-radius: 5px;

    @media (min-width: $tablet) {
        width: auto;
        display: flex;
        justify-content: space-between;
        padding-top: 37px;
        padding-bottom: 37px;
        padding-left: 30px;
        padding-right: 30px;
    }

    @media (min-width: $desktop) {
        width: 1280px;
        margin: 0 auto;
        padding-left: 56px;
        padding-right: 56px;
        padding-top: 70px;
        padding-bottom: 70px;
    }

    &__item {
        position: relative;
        width: 235px;
        font-size: 22px;
        line-height: 160%;
        text-align: center;
        margin: 0 auto;
        margin-bottom: 30px;
        padding-bottom: 32px;
        
        @media (min-width: $tablet) {
            font-size: 20px;
            margin: 0;
            padding-bottom: 0;
            padding-right: 22px;
            padding-left: 22px;
            width: 25%;

        }

        @media (min-width: $desktop) {
            font-size: 22px;
            margin: 0;
            padding: 0;
        }

        

        &::after {
            position: absolute;
            content: '';
            bottom: 0;
            right: 50%;
            margin-right: -57px;
            width: 114px;
            height: 2px;
            background-color: #E7E7E7;

            @media (min-width: $tablet) {
                height: 85px;
                width: 1px;
                right: 0;
                bottom: 50%;
                margin: 0;
                margin-bottom: -42.5px;
            }
            

            @media (min-width: $desktop) {
                height: 114px;
                margin-bottom: -57px;
            }
        }

        

        &:last-child {
            margin-bottom: 0;
            
            @media (min-width: $tablet) {
                padding-right: 0;
            }

            &::after {
                display: none;

            }
        }

        
    }
}


.advantages-wrapper--inner-page {
    position: static;

    @media (min-width: $desktop) {
        display: none;
    }

    @media (min-width: $desktop) {
        margin-bottom: 100px;
    }
}