@import 'vars.scss';


html {
    box-sizing: border-box;
    // scroll-behavior: smooth;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 15px;
    line-height: 160%;
    color: #595959;
    overflow-x: hidden;
    
    // background-color: #FDFDFD;

    @media (min-width: $tablet) {
        font-size: 17px;
    }
}

main {
    display: grid;
    grid-template-columns: 100%;
    row-gap: 40px;

    @media (min-width: $tablet) {
        row-gap: 70px;
    }

    @media (min-width: $desktop) {
        row-gap: 100px;
    }
}

a {
    display: block;
    text-decoration: none;
    cursor: pointer;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

li {
    list-style-type: none; 
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
}



button {
    font: inherit;
    padding: 0;
    margin: 0;
    outline: none;
    background-color: transparent;
    border: none;
    cursor: pointer;
    
    
    
    
}




h1, h2, h3 {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-weight: 400;
    line-height: 105%;
    color: #332B2B;
    margin: 0;
    padding: 0;
}

p {
    margin: 0;
    padding: 0;
}

img {
    display: block;
}

input {
    font: inherit;
    background: transparent;
    margin: 0;
    padding: 0;
}

fieldset {
    margin: 0;
    padding: 0;
    border: none;
}

blockquote {
    margin: 0;
}

tr,
td,
th {
    padding: 0;
}

.show {
    display: block;

    animation-name: menu-slide-open;
    animation-duration: 0.5s;
}

@keyframes menu-slide-open {
    0% {
        top: -220px;
    }

}

@keyframes menu-slide-close {
    0% {
        bottom: 220px;
    }
}

.close {
    animation-name: menu-slide-close;
    animation-duration: 1s;
    display: none;
    
}



.second-title {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 35px;
    font-weight: 400;
    margin-bottom: 20px;

    @media (min-width: $tablet) {
        font-size: 40px;
        margin-bottom: 30px;
    }

    @media (min-width: $desktop) {
        font-size: 50px;
        margin-bottom: 50px;
    }
}

.third-title {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 25px;
    font-weight: 400;

    @media (min-width: $tablet) {
        font-size: 30px;
    }
}


.disable-scroll {
    overflow-y: hidden;
	// position: fixed;
	// left: 0;
	// top: 0;
    // width: 100%;



    @media (min-width: 1280px) {
        padding-right: 17px;
    }
}


