.services {

    padding-left: 15px;
    padding-right: 15px;

    @media (min-width: $tablet) {
        padding-right: 30px;
        padding-left: 30px;
    }

    @media (min-width: $desktop) {
        width: 1280px;
        margin: 0 auto;
        padding: 0;
    }

    &__controls-desktop {
        display: none;

        @media (min-width: $desktop) {
            display: flex;
            width: 207px;
            flex-direction: column;
        }
    }

    &__toggle-desktop {
        color: #595959;

        @media (min-width: $desktop) {
            display: inline-block;
            text-align: right;
            margin-bottom: 40px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    

    &__content-area {
        
        @media (min-width: $desktop) {
            display: flex;
            justify-content: space-between;
            width: 1012px;
        }
    }

    &__list {
        

        @media (min-width: $desktop) {
            display: flex;
            justify-content: space-between;
        }
    }

    

    &__item {
        margin-bottom: 20px;

        @media (min-width: $tablet) {
            margin-bottom: 30px;
        }

        @media (min-width: $desktop) {
            display: none;
        }
    } 

    &__checkbox {
        @media (min-width: $desktop) {
            display: none;
        }
        
    }

    &__toggle-mobile {
        cursor: pointer;
    }

    &__content {

        display: none;

        @media (min-width: $desktop) {
            display: block;
        }
    }

    &__inner {
        padding-top: 30px;

        @media (min-width: $tablet) {
            padding-top: 40px;
        }
      
        @media (min-width: $desktop) {
            width: 1022px;
            padding-top: 0;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }
    }

    
    &__img-wrapper {
        position: relative;
        margin-bottom: 20px;
        padding-top: 10px;
        padding-right: 10px;

        &::before {
            position: absolute;
            content: "";
            width: 98%;
            height: 98%;
            background-color: #E61A23;
            border-radius: 3px;
            top: 0;
            right: 0;
            z-index: -1;

            @media (min-width: $tablet) {
                border-radius: 7px;
            }
        }

        @media (min-width: $tablet) {
            margin-bottom: 30px;
        }

        @media (min-width: $desktop) {
            width: 416px;
            height: 428px;
            margin: 0;
        }
    }

    &__img {
        
        width: 100%;
        height: auto;



        @media (min-width: $tablet) {
            
        }
    
        @media (min-width: $desktop) {
            // order: 1;
        }
    }

    

    &__text {


        @media (min-width: $desktop) {
            width: 576px;
            order: -1;
        }

        &--desktop {
            
        }

    }
    
    
    &__inner--inner {
        @media (min-width: $desktop) {
            display: block;
            width: 1000px;
        }
    }


    &__img-wrapper--inner {
        
        @media (min-width: $desktop) {
            float: right;
            width: 406px;
            height: 505px;
            margin-left: 30px;
            margin-bottom: 50px;
        }
    }

    

    &__text--inner {
        
        @media (min-width: $desktop) {
            margin: 0;
            display: inline;
            padding: 0; 
        }

        span {
            display: none;

            @media (min-width: $desktop) {
                display: block;
                margin-bottom: 20px;
                margin-top: 20px;
            }
        }

        .ordered {
            background-color: coral;
        }

        

            
        
    }


    &__text-dots {
        position: relative;


        &:before {
            position: absolute;
            content: '';
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: #E61A23;
            top: 8px;
            left: -20px;
        }
    }

}




.services__checkbox:checked ~ .services__toggle-mobile {
    color: #332B2B;
}

.services__checkbox:checked ~ .services__content {
    display: block;
}

.services__toggle-mobile {
    position: relative;

    @media (min-width: $desktop) {
        display: none;
    }
}

.services__checkbox:checked ~ .services__toggle-mobile::after {
    position: absolute;
    content: '';
    width: 150%;
    height: 1px;
    bottom: -10px;
    right: 0;
    background-color: #E61A23;

    @media (min-width: $tablet) {
        width: 100%;
    }
}

.services__checkbox:checked ~ .services__map {
    margin-top: 35px;

    @media (min-width: $tablet) {
        margin-top: 40px;
    }
}

.toggle-active {
    position: relative;
    color: #332B2B;

    &::after {
        position: absolute;
        content: '';
        width: 100%;
        height: 1px;
        bottom: -10px;
        left: 0;
        background-color: #E61A23;
    }
}

.item-active {
    @media (min-width: $desktop) {
        display: block;
        
    }
}



