.article {


    

    


    &__title {

        display: inline-block;
        font-size: 25px;
        color: #fff;
        background-color: #E61A23;
        padding-top: 6px;
        padding-bottom: 8px;
        padding-left: 23px;
        padding-right: 23px;
        border-radius: 5px 5px 0px 0px;


        @media (min-width: $tablet) {
            font-size: 30px;

        }
    
        @media (min-width: $desktop) {
            font-size: 40px;
        }

        
    }

    &__img-wrapper {

        
    }




    &__img {
        width: 100%;
        height: auto;

        
    }

    &__text-wrapper {
        padding-left: 15px;
        padding-right: 15px;
        padding-top: 15px;
        padding-bottom: 20px;

        @media (min-width: $tablet) {
            padding-left: 20px;
            padding-right: 20px;
            padding-top: 20px;
            padding-bottom: 30px;
        }

        
    }

    &__text {
        color: #595959;
        margin-bottom: 10px;

        
    }

    &__link {
        font-size: 17px;
        line-height: 105%;
        color: #E61A23;

        @media (min-width: $tablet) {
            font-size: 18px;
        }

        @media (min-width: $desktop) {
            font-size: 20px;
        }
    }
}



