.coach {

    
    padding-right: 15px;
    padding-left: 15px;

    @media (min-width: $tablet) {
        padding-right: 30px;
        padding-left: 30px;
    }

    @media (min-width: $desktop) {
        width: 1280px;
        margin: 0 auto;
        padding: 0;
    }

    &__wrapper {
        
        @media (min-width: $desktop) {
            display: flex;
            justify-content: space-between;
        }
    }

    &__title {
        margin-bottom: 20px;

        @media (min-width: $tablet) {
            margin-bottom: 30px;
        }

        @media (min-width: $desktop) {
            margin-bottom: 70px;
        }
    }

    &__img-wrapper {
        position: relative;
        margin-bottom: 20px;
        padding-bottom: 10px;
        padding-left: 10px;
        margin-bottom: 45px;

        &::before {
            position: absolute;
            content: "";
            width: 98%;
            height: 98%;
            background-color: #E61A23;
            border-radius: 3px;
            bottom: 0;
            left: 0;
            z-index: -1;

            @media (min-width: $tablet) {
                border-radius: 7px;
            }
        }

        @media (min-width: $tablet) {
            margin-bottom: 30px;
        }

        @media (min-width: $desktop) {
            margin-bottom: 0;
            width: 407px;
            height: 509px;
        }
    }

    &__photo {
        width: 100%;
        height: auto;

        @media (min-width: $tablet) {

        }

        @media (min-width: $desktop) {
            width: 100%;
            height: 100%;
        }
    }

    

    &__name {
        font-family: 'Yanone Kaffeesatz', sans-serif;
        font-size: 25px;
        font-weight: 400;
        color: #332B2B;
        margin-bottom: 10px;

        @media (min-width: $tablet) {
            margin-bottom: 20px;
        }

        @media (min-width: $desktop) {
            margin-bottom: 9px;
            font-size: 30px;
        }
    }

    &__text-wrapper {
        
        @media (min-width: $desktop) {
            width: 840px;
            display: flex;
            justify-content: space-between;
        }
    }

    &__text {
        margin-bottom: 10px;
        

        &:last-child {
            margin-bottom: 0;
        }

        @media (min-width: $tablet) {
            font-size: 17px;
            margin-bottom: 20px;
        }

        @media (min-width: $desktop) {
            font-size: 17px;
            width: 405px;
        }
    }
}