.menu {

    width: 100%;
    box-shadow: 0px 20px 20px rgba(189, 185, 185, 0.15);

    @media (min-width: $desktop) {
        width: 1091px;
        display: block;
        box-shadow: none;
    }


    &__list {
        width: 100%;
        background-color: #fff;
        padding-top: 40px;
        padding-bottom: 40px;
        text-align: center;

        @media (min-width: $tablet) {
            padding-top: 70px;
            padding-bottom: 70px;
        }

        @media (min-width: $desktop) {
            display: flex;
            justify-content: space-between;
            padding: 0;
        }
    }

    &__item {
        margin-bottom: 30px;

        &:last-child {
            margin-bottom: 0;
            @media (min-width: $desktop) {
                margin-right: 0;
            }
        }

        @media (min-width: $desktop) {
            margin: 0;
        }
    }

    &__link {
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: #332B2B;

        @media (min-width: $tablet) {
            font-size: 20px;
        }

        @media (min-width: $desktop) {
            font-size: 16px;
        }

        &:hover,
        &:focus {
            opacity: 0.8;
        }

        &:active {
            opacity: 0.6;
        }
    }

}