.tables {



    &--inner-page {
        
        @media (min-width: $tablet) {
        }

        @media (min-width: $desktop) {
        }
    }

    @media (min-width: $tablet) {
    }

    @media (min-width: $desktop) {
        width: 1280px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
    }

    &__price-widt {
        @media (min-width: $tablet) {
            width: 50%;
        }
    }

    &__wrapper {

        
        margin-bottom: 40px;

        &:last-child {
            margin-bottom: 0;
        }

        @media (min-width: $tablet) {
            padding-right: 30px;
            padding-left: 30px;
            
        }

        @media (min-width: $desktop) {
            padding: 0;
        }
    }

    &__title {
        margin-bottom: 20px;
        padding-right: 4.69%;
        padding-left: 4.69%;

        @media (min-width: $tablet) {
            padding: 0;
            
        }

        @media (min-width: $desktop) {
            padding: 0;
            margin-bottom: 50px;
        }
    }

    
    
    

    &__table {

        border-collapse: collapse;
        font-size: 14px;
        line-height: 160%;
        width: 100%;
        max-width: 624px;
        border: 1px solid #e7e7e7;
        margin: 0 auto;

        @media (min-width: $tablet) {
            font-size: 17px;
            width: 624px;
            margin: 0;
            
        }

        th {
            border: 1px solid #e7e7e7;
            text-align: left;
            vertical-align: top;
            padding-left: 5px;
            padding-top: 10px;
            padding-right: 5px;
            padding-bottom: 10px;
            
            @media (min-width: $tablet) {
                padding-left: 20px;
            }
        }

        &--days {
            th {
                &:first-child {
                    border: none;
                }
            }

            td {

                @media (min-width: $tablet) {
                    padding-top: 23px;
                }
            }

            @media (min-width: $tablet) {
                padding-top: 11px;
            }
        }

        &--price {

            @media (min-width: $tablet) {
                padding-top: 10px;
                padding-bottom: 13px;
            }
        }
    
        td {
            border-bottom: 1px solid #e7e7e7;
            border-right: 1px solid #e7e7e7;
            text-align: left;
            vertical-align: top;
            padding-left: 5px;
            padding-top: 10px;
            padding-right: 5px;
            padding-bottom: 10px;

            @media (min-width: $tablet) {
                padding-left: 20px;
                padding-top: 23px;
            }
        }
    
        

        
    }

    
    &__left-col {
        @media (min-width: $tablet) {
            width: 264px;
        }
    }
    
    
}


