.about {
    padding-right: 15px;
    padding-left: 15px;

    @media (min-width: $tablet) {
        padding-left: 30px;
        padding-right: 30px;
    }

    @media(min-width: $desktop) {
        padding: 0;
        font-size: 17px;
        width: 1280px;
        margin: 0 auto;
    }

    &__title {
        margin-bottom: 20px;

        @media (min-width: $tablet) {
            position: relative;
            display: inline-block;
            font-size: 40px;
            margin-bottom: 53px;
            color: #fff;

            &::before {
                position: absolute;
                content: '';
                height: 79px;
                width: 2000px;
                background-color: #E61A23;
                right: 0;
                top: -14px;
                right: -51px;
                border-radius: 5px;
                z-index: -1;
            }
        }

        @media (min-width: $desktop) {
            margin-bottom: 71px;
            font-size: 50px;

            &::before {
                height: 87px;
                right: 0;
                top: -14px;
                right: -58px;
                
            }
        }
    }

    &__inner {

        @media (min-width: $desktop) {
            display: flex;
            justify-content: space-between;
        }
    }

    &__text-wrapper {

        @media (min-width: $tablet) {
            font-size: 17px;
        }
    }

    &__text {
        margin-bottom: 10px;

        @media (min-width: $tablet) {
            margin-bottom: 20px;
        }

        @media (min-width: $desktop) {
            
            width: 623px;
            margin-bottom: 15px;
        }
    }

    &__link {
        display: inline-block;
        position: relative;
        font-weight: 500;
        color: #E61A23;
        margin-bottom: 20px;


        @media (min-width: $tablet) {

        }

        @media (min-width: $desktop) {
        }
        
        &::after {
            position: absolute;
            content: '';
            width: 100%;
            height: 1px;
            background-color: #E61A23;
            bottom: 1px;
            left: 0;
        }

        
    }

    &__video {
        width: 100%;
        height: auto;
        border: none;
        
        @media (min-width: $tablet) {
            height: auto;
        }

        @media (min-width: $desktop) {
            width: 624px;
            height: auto;
        }
    }
}