.main-header {
    
    top: 0;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    
    @media (min-width: $tablet) {
    }
    
    &--page-inner {
        margin-bottom: 9px;

        @media (min-width: $tablet) {
            margin-bottom: 30px;
        }

        @media (min-width: $desktop) {
            margin-bottom: 40px;
        }
    }

    &__inner {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        padding-top: 10px;
        padding-bottom: 11px;
        padding-right: 15px;
        padding-left: 15px;
        width: 100%;
        background-color: #fff;

        @media (min-width: $tablet) {
            padding-left: 30px;
            padding-right: 30px;
            padding-top: 23px;
            padding-bottom: 12px;
        }

        @media (min-width: $desktop) {
            width: 1280px;
            margin: 0 auto;
            padding: 0;
            padding-top: 16px;
            padding-bottom: 18px;
        }
    }

    &__menu {
        position: absolute;
        top: 52px;
        left: 0;
        z-index: 5;
        


        @media (min-width: $tablet) {
            top: 85px;
        }

        @media (min-width: $desktop) {
            position: static;
        }
    }

    

    &__contacts {

        display: none;

        @media (min-width: $desktop) {
            display: flex;
            align-items: center;
            background-color: #E61A23;
            width: 100%;
        }
    }

    

    &__logo-img {
        @media (min-width: $tablet) {
            width: 126.83px;
            height: 51.24px;
        }
    }

    &__icon-wrapper {
        width: 32px;
        height: 23px;

        
    }

    &__icon {
        fill: #332B2B;
    }

    &__menu-close {
        z-index: 100;
        outline-width: 0;
            outline: none;

        @media (min-width: $desktop) {
            display: none;
        }

    }

    &__menu-open {
        position: relative;

        z-index: 110;
        outline-width: 0;
        outline: none;

        &:focus {
            outline-width: 0;
            outline: none;
        }

        @media (min-width: $desktop) {
            display: none;
        }

    }


    &__callback-inner,
    &__callback {
        

        display: flex;
        stroke: #1A2E3B;
        
        
        @media (min-width: $desktop) {
            display: none;
        }
    }

    &__icon {
        margin-left: auto;
    }

}



.main-header--inner {
    margin-bottom: 20px;

    @media (min-width: $tablet) {
        margin-bottom: 30px;
    }

    @media (min-width: $desktop) {
        margin-bottom: 40px;
    }
}



.main-header__menu-open {
    // display: flex;
    position: relative;
    margin: 0;
    padding: 0;
    width: 32px;
    height: 22px;
    cursor: pointer;
  }
  
  .burger,
  .burger:after,
  .burger:before {

    width: 32px;
    height: 2px;
  }
  
  .burger {
    position: absolute;
    top: -10px;
    left: 0;
    
    transform: translateY(25px);
    background: #332B2B;
    transition: all 0ms 200ms;
  }
  .burger.animate {
    background: rgba(255, 255, 255, 0);
  }
  
  .burger:before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 8px;
    background: #332B2B;
    transition: bottom 200ms 200ms cubic-bezier(0.23, 1, 0.32, 1), transform 200ms cubic-bezier(0.23, 1, 0.32, 1);
  }
  
  .burger:after {
    content: "";
    position: absolute;
    left: 0;
    top: 8px;
    background: #332B2B;
    transition: top 200ms 200ms cubic-bezier(0.23, 1, 0.32, 1), transform 200ms cubic-bezier(0.23, 1, 0.32, 1);
  }
  
  .burger.animate:after {
    top: 0;
    transform: rotate(45deg);
    transition: top 200ms cubic-bezier(0.23, 1, 0.32, 1), transform 200ms 200ms cubic-bezier(0.23, 1, 0.32, 1);
  }
  
  .burger.animate:before {
    bottom: 0;
    transform: rotate(-45deg);
    transition: bottom 200ms cubic-bezier(0.23, 1, 0.32, 1), transform 200ms 200ms cubic-bezier(0.23, 1, 0.32, 1);
  }
  