.article-page {

    


    @media (min-width: $tablet) {
        
    }

    @media (min-width: $desktop) {
        width: 1280px;
        margin: 0 auto;
        padding: 0;
    }

    &__main-title {
        margin-top: 10px;
        margin-bottom: 20px;
        padding-left: 15px;
        padding-right: 15px;
        
        @media (min-width: $tablet) {
            margin-bottom: 20px;
            margin-top: 23px;
            padding-left: 30px;
            padding-right: 30px;
        }        

        @media (min-width: $desktop) {
            padding: 0;
            margin-bottom: 30px;
            margin-top: 30px;
        }
    }

    &__wrapper {
        box-shadow: 0px 8px 25px rgba(220, 220, 220, 0.25);
        border-radius: 5px;
        padding-bottom: 20px;
    }

    &__title {
        display: inline-block;
        font-size: 25px;
        color: #fff;
        padding-left: 23px;
        padding-right: 23px;
        padding-top: 6px;
        padding-bottom: 8px;
        background-color: #E61A23;
        border-radius: 5px 5px 0px 0px;

        



        @media (min-width: $tablet) {
            
            margin-left: 30px;
            margin-right: 30px;
        }        

        @media (min-width: $desktop) {
            display: block;
            font-size: 40px;
            margin-bottom: 20px;
            margin: 0;
            padding: 0;
            background-color: transparent;
            color: #595959;
        }
    }

    

    &__img-wrapper {
        height: auto;
        margin-bottom: 15px;
        

        @media (min-width: $tablet) {
            
            left: 0;
            right: 0;
            width: 100%;
            margin: 0 auto;
            margin-bottom: 30px;
            padding-left: 30px;
            padding-right: 30px;
        }

        @media (min-width: $desktop) {

            width: 624px;
            height: auto;
            margin: 0;
            margin-left: 30px;
            margin-bottom: 50px;
            float: right;
        }
    }

    &__img {
        width: 100%;
        height: auto;
    }

    &__text {
        padding-left: 15px;
        padding-right: 15px;

        @media (min-width: $tablet) {
            padding-left: 30px;
            padding-right: 30px;
        }

        @media (min-width: $desktop) {
            padding: 0;
        }
    }
    
    span {
        display: block;
        margin-bottom: 10px;
        margin-top: 10px;

        @media (min-width: $tablet) {
            margin-top: 20px;
            margin-bottom: 20px;

        }
    }

    
    b {
        display: block;
        margin-bottom: 10px;
        margin-top: 10px;

        @media (min-width: $tablet) {
            margin-top: 20px;
            margin-bottom: 20px;

        }
    }
    
}




.article-page__text {

    
}

.article-page__list {
    display: inline-block;

    margin-left: 15px;
    margin-right: 15px;

    @media (min-width: $tablet) {
        padding-left: 30px;
        padding-right: 30px;
    }

    @media (min-width: $desktop) {
        padding: 0;
    }

    li {
        position: relative;
        margin-bottom: 20px;

        &:before {
            position: absolute;
            content: '';
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: #E61A23;
            top: 8px;
            left: -20px;
        }
    }
}

