.adress {

    padding-left: 15px;
    padding-right: 15px;

    @media (min-width: $tablet) {
        padding-right: 30px;
        padding-left: 30px;
    }

    @media (min-width: $desktop) {
        width: 1280px;
        margin: 0 auto;
        padding: 0;
    }

    &__controls-desktop {
        display: none;

        @media (min-width: $desktop) {
            display: flex;
            width: 207px;
            flex-direction: column;
        }
    }

    &__toggle-desktop {
        color: #595959;

        @media (min-width: $desktop) {
            display: inline-block;
            text-align: right;
            margin-bottom: 40px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    

    &__content-area {
        
        @media (min-width: $desktop) {
            display: flex;
            justify-content: space-between;
            width: 1012px;
        }
    }

    &__list {
        

        @media (min-width: $desktop) {
            display: flex;
            justify-content: space-between;
        }
    }

    &__wrapper {

        

        // @media (min-width: $desktop) {
        //     width: 685px;
        //     height: 400px;
        // }
    
    }

    &__item {
        margin-bottom: 20px;

        @media (min-width: $tablet) {
            margin-bottom: 30px;
        }

        @media (min-width: $desktop) {
            display: none;
        }
    } 

    &__checkbox {
        @media (min-width: $desktop) {
            display: none;
        }
        
    }

    &__toggle-mobile {
        cursor: pointer;
    }

    &__content {

        display: none;

        @media (min-width: $desktop) {
            display: block;
        }
    }

    &__inner {
        position: relative;
        padding-top: 30px;

        @media (min-width: $tablet) {
            padding-top: 40px;
        }
      
        @media (min-width: $desktop) {
            padding-top: 0;
        }
    }

    &__map-pic {
        position: absolute;
        left: 0;
        top: 30px;
        z-index: -1;
        width: 100%;
        height: 198px;

        @media (min-width: $tablet) {
            top: 40px;
        }
      
        @media (min-width: $desktop) {
            top: 0;
        }
    }
    

    &__map {
        
        width: 100vw;
        position: relative;
        left: 50%;
        right: 50%;
        margin-left: -50vw;
        margin-right: -50vw;
        margin-bottom: 20px;
        border: none;

        height: 320px;
        border: none;

        iframe {
            border: none;
            font-size: 0;
        }

        @media (min-width: $tablet) {
            width: 100%;
            height: 438px;
            position: static;
            margin: 0;
        }
    
        @media (min-width: $desktop) {
            width: 685px;
            height: 424px;
        }
    }

    &__text-wrapper {

        
        @media (min-width: $desktop) {
            display: none;

        }

        &--desktop {
            display: none;

            @media (min-width: $desktop) {
                display: block;
                width: 297px;
            }
        }
        
    }

    &__text {


        &--bold {
            font-weight: 600;
        }

        
    }
    
    

}



.adress__checkbox:checked ~ .adress__toggle-mobile {
    color: #332B2B;
}

.adress__checkbox:checked ~ .adress__content {
    display: block;
}

.adress__toggle-mobile {
    position: relative;

    @media (min-width: $desktop) {
        display: none;
    }
}

.adress__checkbox:checked ~ .adress__toggle-mobile::after {
    position: absolute;
    content: '';
    width: 150%;
    height: 1px;
    bottom: -10px;
    right: 0;
    background-color: #E61A23;

    @media (min-width: $tablet) {
        width: 100%;
    }
}

.adress__checkbox:checked ~ .adress__map {
    margin-top: 35px;

    @media (min-width: $tablet) {
        margin-top: 40px;
    }
}

.toggle-active {
    position: relative;
    color: #332B2B;

    &::after {
        position: absolute;
        content: '';
        width: 100%;
        height: 1px;
        bottom: -10px;
        left: 0;
        background-color: #E61A23;
    }
}

.item-active {
    @media (min-width: $desktop) {
        display: block;
        
    }
}