@import url(global/normalize.css);
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 160%;
  color: #595959;
  overflow-x: hidden;
}

@media (min-width: 768px) {
