.main-footer {


    margin-top: 40px;
    padding-top: 20px;
    padding-bottom: 20px;
    background-color: #f1f1f1;
    text-align: center;

    @media (min-width: $tablet) {
        margin-top: 70px;
        padding-top: 60px;
    }

    @media (min-width: $desktop) {
        margin-top: 100px;
        padding-bottom: 33px;
    }
    
    

    &__wrapper {
        @media (min-width: $tablet) {
        }

        @media (min-width: $desktop) {
            width: 1280px;
            margin: 0 auto;
            text-align: left;
        }
    }
    

    &__logo {
        display: inline-block;
        margin-bottom: 20px;
        text-align: center;

        @media (min-width: $tablet) {
            width: 168px;
            height: 92px;
        }

        @media (min-width: $desktop) {
            width: 130px;
            height: auto;
        }
    }

    &__img {

        @media (min-width: $tablet) {
            width: 100%;
        }
    }

    &__inner {

        display: grid;
        grid-template-rows: 1fr;
        row-gap: 30px;
        

        @media (min-width: $tablet) {
            
        }

        @media (min-width: $desktop) {
            width: 1280px;
            padding-right: 122px;
            // grid-template-columns: repeat(4, 1fr);
            display: flex;
            justify-content: space-between;
        }
    }

    &__phones {
        font-family: 'Carter One', cursive;
        font-size: 16px;
        line-height: 160%;
        text-align: center;
        letter-spacing: 0.11em;

        @media (min-width: $tablet) {
            font-size: 20px;
        }

        @media (min-width: $desktop) {
            font-size: 16px;
            margin: 0;
            text-align: left;
        }
    }

    &__phone {
        

        a {
            color: #332B2B;
        }
    }

    &__group-title {
        font-family: 'Montserrat', sans-serif;
        font-size: 15px;
        font-weight: 600;
        letter-spacing: 0.11em;
        color: #332B2B;
        margin-bottom: 20px;
        text-align: center;

        @media (min-width: $tablet) {
            font-size: 16px;
            line-height: 20px;
        }

        @media (min-width: $desktop) {
            text-align-last: left;
        }
        
    }

    &__nav {

        @media (min-width: $tablet) {
        }

        @media (min-width: $desktop) {
            margin: 0;
        }
    }

    &__nav-list {
        
    }

    &__nav-item {
        margin-bottom: 20px;

        
    }

    &__nav-link {
        line-height: 18px;
        color: inherit;

        &:hover,
        &:focus {
            opacity: 0.8;
        }

        &:active {
            opacity: 0.6;
        }

        @media (min-width: $tablet) {
            font-size: 17px;
            line-height: 21px;
        }

        @media (min-width: $desktop) {
            font-size: 16px;
            line-height: 20px;
        }
    }

    &__partners {

    }

    &__partners-inner {
        width: 270px;
        display: flex;
        justify-content: space-between;
        margin: 0 auto;

        @media (min-width: $tablet) {
            width: 280px;
            margin: 0 auto;
        }
    }

    &__socials {

        @media (min-width: $tablet) {
        }


        @media (min-width: $desktop) {
            margin: 0;
        }
    }

    &__socials-list-item {
        margin-bottom: 20px;

        
    }

    &__social-link {
        color: inherit;

        &:hover,
        &:focus {
            opacity: 0.8;
        }

        &:active {
            opacity: 0.6;
        }
    }

    &__copyright-wrapper {
        position: relative;
        font-size: 12px;
        line-height: 15px;
        padding-top: 20px;

        @media (min-width: $tablet) {
            font-size: 14px;
            line-height: 17px;
        }


        @media (min-width: $desktop) {
        }

        &::before {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            height: 1px;
            width: 100%;
            background-color: #E7E7E7;
        }
    }
}