.intro {


    @media (min-width: $tablet) {
    }

    @media (min-width: $desktop) {
        position: relative;
        margin-bottom: 60px;
        background-image: url(../img/intro-desktop.jpg);
        background-size: cover;
        background-position: top center;
        background-repeat: no-repeat;
        margin-bottom: 130px;
        z-index: 1;

        &::after {
            position: absolute;
            content: '';
            width: 100%;
            height: 100%;
            background-color: rgba(39, 39, 39, 0.5);;
            left: 0;
            top: 0;
            z-index: -1;
        }
    }

    &__bg-block {
        display: none;

        @media (min-width: $tablet) {
            position: relative;
            display: block;
            margin-bottom: 50px;
            width: 100%;
            height: 372px;
            background-color: #272727;
            background-image: url(../img/intro-tablet.jpg);
            background-repeat: no-repeat;
            background-size: cover;
            background-position: top center;
        }

        @media (min-width: $desktop) {
            display: none;
        }
    }

    &__wrapper {

        @media (min-width: $desktop) {
            padding-top: 215px;
        }
    }

    &__container {

        @media (min-width: $desktop) {
            width: 1280px;
            margin: 0 auto;
            padding-bottom: 393px;
        }
    }

    &__text-wrapper {

        margin-bottom: 20px;
        
        @media (min-width: $tablet) {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 506px;
            margin: 0 auto;
            margin-bottom: 70px;
        }

        @media (min-width: $desktop) {
            width: 521px;
            margin: 0;
        }

    }

    &__advantages-wrapper {
        
        margin: 0 auto;
        width: 100%;

        @media (min-width: $desktop) {
            position: absolute;
            bottom: -105px;
        }
    }

    

    
    
    &__title-wrapper {
        position: relative;
        padding-top: 99px;
        padding-bottom: 20px;
        padding-right: 15px;
        padding-left: 15px;
        margin-bottom: 20px;

        background-color: #272727;
        background-image: url(../img/intro-mobile.jpg);

        background-repeat: no-repeat;
        background-size: cover;
        background-position: top center;
        z-index: 1;

        &::after {
            position: absolute;
            content: '';
            width: 100%;
            height: 100%;
            background-color: rgba(39, 39, 39, 0.5);;
            left: 0;
            top: 0;
            z-index: -1;

            @media (min-width: $tablet) {
                display: none;
            }
        }

        

        @media (min-width: $tablet) {
            background: none;
            padding: 0;
        }

        @media (min-width: $desktop) {
            margin-bottom: 0;
        }
    }

    &__title {

        font-family: 'Yanone Kaffeesatz', sans-serif;
        font-size: 40px;
        line-height: 105%;
        font-weight: 400;
        color: #FEFEFE;

        @media (min-width: $tablet) {
            font-size: 50px;
            color: #E61A23;
        }

        @media (min-width: $desktop) {
            color: #fff;
            font-size: 75px;
            width: auto;
        }
    }

    &__inner {
        display: flex;
        flex-wrap: wrap;
        padding-left: 15px;
        padding-right: 15px;

        @media (min-width: $tablet) {
            padding: 0;
            text-align: center;
        }

        @media (min-width: $desktop) {
            
        }
    }

    &__text {
        font-size: 15px;
        font-weight: 600;
        line-height: 160%;
        margin-bottom: 20px;

        @media (min-width: $tablet) {
            font-size: 20px;
            color: #332B2B;
            margin: 0 auto;
            margin-bottom: 20px;
        }

        @media (min-width: $desktop) {
            color: #fff;
            font-size: 17px;
            text-align: left;
            margin-bottom: 36px;
        }
    }

    &__button {
        background-color: #E61A23;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        text-transform: uppercase;
        color: #fff;

        padding-top: 16px;
        padding-bottom: 17px;
        padding-left: 43px;
        padding-right: 43px;

        margin: 0 auto;

        border-radius: 5px;

        &:hover,
        &:focus {
            background-color: #E4000A;
            transition: 0.2s;    
        }

        &:active {
            background-color: #D0060F;    
        }

        @media (min-width: $tablet) {
            padding-top: 24px;
            padding-bottom: 24px;
            padding-left: 98px;
            padding-right: 108px;
        }

        @media (min-width: $desktop) {
            padding-left: 35px;
            padding-right: 36px;
            padding-top: 16px;
            padding-bottom: 17px;
            margin: 0;
        }
    }
}