.gallery {
    position: relative;
    
    @media (min-width: $tablet) {
        padding-top: 50px;
    }

    @media (min-width: $desktop) {
    }


    &::before {
        @media (min-width: $tablet) {
            position: absolute;
            content: '';
            width: 100%;
            height: 300px;
            top: 0;
            background-color: #E61A23;
            z-index: -1;
        }
    }


    

    &__title {
        margin-bottom: 20px;
        padding-right: 15px;
        padding-left: 15px;

        @media (min-width: $tablet) {
            color: #fff;
            margin-bottom: 40px;
        }

        @media (min-width: $desktop) {
            padding: 0;
            width: 1280px;
            margin: 0 auto;
            margin-bottom: 40px;
        }
    }


    &__slide {
        margin-left: 5px;
        margin-right: 5px;
        

        @media (min-width: $tablet) {
            width: 515px;
            height: 343px;
            margin-left: 13.5px;
            margin-right: 13.5px;
        }
    }

    &__img {
        border-radius: 5px;

        @media (min-width: $tablet) {
            width: 100%;
            height: auto;
        }
    }
}