.achievements {


    @media (min-width: $tablet) {
        padding: 0;
    }
    
    &__title-wrapper {
        // background-color: rosybrown;

        @media (min-width: $tablet) {
            position: relative;

            &::before {
                position: absolute;
                content: '';
                height: 87px;
                // width: 50vw;
                width: 46vw;
                
                background-color: #E61A23;
                right: 0;
                top: -20px;
                // left: -58px;
                border-radius: 5px 0px 0px 5px;
                z-index: -1;


            }
        }

        @media (min-width: $desktop) {
            position: relative;

            &::before {
                width: 33vw;

                
            }
        }

        
    }

    &__title-inner {
        padding-right: 15px;
        padding-left: 15px;

        @media (min-width: $tablet) {
            display: flex;
            padding-right: 30px;
            padding-left: 30px;
            // background-color: salmon;
            width: 100%;
            
        }

        @media (min-width: $desktop) {
            padding: 0;
            width: 1280px;
            margin: 0 auto;
        }
    }

    &__title {
        
        
        // background-color: blue;
        

        @media (min-width: $tablet) {
            display: inline-block;
            // padding: 0;
            margin-bottom: 71px;
            margin-left: auto;
            color: #fff;

            
        }   

        
    }

    &__wrapper {

        position: relative;

        margin-top: 20px;
        margin-bottom: 20px;

        @media (min-width: $tablet) {
            margin-bottom: 40px;
        }

        @media (min-width: $desktop) {
            width: 1280px;
            margin: 0 auto;
            padding-top: 20px;
            margin-bottom: 70px;
        }
    }

    &__slide {
        width: 290px;
        height: auto;
        margin: 0 auto;
        margin-left: 5px;
        margin-right: 5px;
        

        background-color: #FFFFFF;
        color: #332B2B;
        border-radius: 5px;
        box-shadow: 0px 0px 14.1872px rgba(189, 185, 185, 0.25);

        @media (min-width: $tablet) {
            margin-left: 15px;
            margin-right: 15px;
            width: 405px;
        }

        @media (min-width: $desktop) {
            width: 406px;
        }
    }

    &__text  {
        padding-top: 5px;
        padding-left: 15px;
        padding-right: 15px;
        padding-bottom: 20px;

        @media (min-width: $tablet) {
            padding-left: 20px;
            padding-right: 20px;
            padding-bottom: 40px;
        }
    }

    &__card-title {
        font-family: 'Yanone Kaffeesatz', sans-serif;
        font-size: 25px;
        line-height: 40px;
        font-weight: 400;
        margin-bottom: 5px;

        @media (min-width: $tablet) {
            margin-bottom: 15px;
            font-size: 30px;
        }
    }

    &__card-text {

        @media (min-width: $tablet) {
            font-size: 17px;
        }
    }

    &__img-wrapper {
        position: relative;
        margin-bottom: 5px;

        @media (min-width: $tablet) {
            margin-bottom: 10px;
        }
    }

    &__img {
        border-radius: 5px;

        @media (min-width: $tablet) {
            width: 100%;
            height: auto;
        }

        @media (min-width: $desktop) {
            width: 406px;
            height: 415px;
        }
    }

    &__date {
        position: absolute;
        bottom: 0;
        right: 0;
        font-weight: 500;
        font-size: 17px;
        color: #fff;
        background-color: #E61A23;
        padding-top: 6px;
        padding-bottom: 5px;
        padding-left: 34px;
        padding-right: 34px;
        border-radius: 3px 0px;
    }
}




.swiper-button-prev {
    display: none;

    &::after {
        display: none;
    }
    

    @media (min-width: 1595px) {
        display: block;
        position: absolute;
        left: -114px;
        z-index: 10;
        outline: none;
    }

    
}

.swiper-button-next {
    display: none;
    
    &::after {
        display: none;
    }

    @media (min-width: $desktop) {
        display: block;
        position: absolute;
        right: -114px;
        z-index: 10;
        outline: none;
    }

    
}

.swiper__arrow-left {
    stroke: #595959;
    
}

.swiper__arrow-right {
    stroke: #595959;
    
}