.form {
    position: relative;

    position: fixed;
    width: 320px;
    text-align: center;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 10vw;
    box-shadow: 0px 4px 25px rgba(179, 179, 179, 0.25);
    z-index: 10;

    @media (min-width: $tablet) {
        width: 706px;
        top: 180px;
    }

    @media (min-width: $desktop) {
        width: 1280px;
        top: 245px;
    }

    &__wrapper {
        position: relative;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        padding-left: 15px;
        padding-right: 15px;
        padding-bottom: 50px;
        padding-top: 55px;
        background-color: #fff;

        @media (min-width: $tablet) {
            padding-top: 70px;
            padding-bottom: 70px;
            padding-left: 76px;
            padding-right: 76px;
        }

        
    }

    &__btn-close {
        position: absolute;
        width: 25px;
        height: 25px;
        top: 0;
        right: 0;
        margin-right: 20px;
        margin-top: 20px;

        &:hover,
        &:focus {
            opacity: 0.7;
        }

        @media (min-width: $desktop) {
            margin-right: 40px;
            margin-top: 30px;
        }
    }

    &__close-icon {
        margin-left: auto;
        fill: #332B2B;
        width: 25px;
        height: 25px;

        
    }

    &__text-wrapper {
        text-align: center;
        
        @media (min-width: $tablet) {
            width: 553px;
            background-color: #fff;
        }
    }

    &__title {
        margin-bottom: 10px;

        @media (min-width: $tablet) {
            margin-bottom: 20px;
        }

        @media (min-width: $desktop) {
        }
    }

    &__text {
        font-family: 'Yanone Kaffeesatz', sans-serif;
        font-size: 25px;
        line-height: 105%;
        font-weight: 400;
        margin-bottom: 20px;

        @media (min-width: $tablet) {
            font-size: 30px;
            margin-bottom: 30px;
        }

        @media (min-width: $desktop) {
            margin-bottom: 35px;
        }
    }

   &__main-form {
       display: flex;
       flex-wrap: wrap;

       @media (min-width: $tablet) {
           width: 464px;
       }
   }

    &__input {
        display: block;
        width: 100%;
        border: 1px solid #DBDBDB;
        border-radius: 5px;
        font-size: 17px;
        padding-top: 15px;
        padding-bottom: 14px;
        padding-left: 20px;
        padding-right: 20px;

        margin-bottom: 10px;

        outline: none;

        &::placeholder {
            color: #595959;
        }

        
        
        &:focus {
            &::placeholder {
                color: #DBDBDB;
            }
            border-color: #595959;
            
        }

        @media (min-width: $tablet) {
            font-size: 22px;
            margin-bottom: 15px;
        }

        @media (min-width: $desktop) {

        }
    }


    &__btn {
        width: 100%;
        background-color: #E61A23;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        text-transform: uppercase;
        color: #fff;

        padding-top: 21px;
        padding-bottom: 20px;

        margin: 0 auto;

        border-radius: 5px;

        @media (min-width: $tablet) {
            font-size: 18px;
            padding-top: 24px;
            padding-bottom: 24px;
        }

        @media (min-width: $desktop) {
            margin: 0;
        }
    }
}



.form--popup {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 50px;
    padding-top: 20px;
    top: 52px;
    box-shadow: 0px 4px 25px rgba(179, 179, 179, 0.25);
    
}






.form-inner {
    position: static;

    .form__wrapper {
    }
}






// new

.section {
	min-height: 500px;
	background-color: aqua;
	padding: 50px;
}

.section-2 {
	min-height: 700px;
	background-color: navy;
}

.btn {
	border: none;
	cursor: pointer;
	background-color: #000;
	color: #fff;
	padding: 10px;
}

.modal-overlay {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.7);
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
	width: 100%;
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s ease-in-out;
}

.modal {
	background-color: #fff;
	width: 300px;
	height: 300px;
	display: flex;
	align-items: center;
	justify-content: center;
	display: none;
}

.modal-overlay--visible {
	opacity: 1;
	visibility: visible;
	transition: all 0.3s ease-in-out;
}

.modal--visible {
	display: block;
}

.disable-scroll {
	position: relative;
	overflow: hidden;
	height: 100vh;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
}